<template>
  <div class="history-item">
    <span class="email" v-html="emailWithBreak"></span>
    <a
      href="javascript:void(0);"
      class="btn"
      v-clipboard:copy="historyItem.email"
      v-clipboard:success="onCopy"
      title="Copy to clipboard"
    >
      <svg
