        />
      </svg>
    </a>
    <a
      :href="inboxUrl"
      class="btn"
      target="_blank"
      rel="noopener noreferrer"
      v-on:click="onInboxClick"
      title="Go to inbox"
    >
      <svg
        aria-hidden="true"
